'use client'

import React, { useState, useEffect } from 'react'
import {
	Dialog,
	DialogContent,
	DialogHeader,
	DialogTitle,
} from '@/components/ui/dialog'
import { Input } from '@/components/ui/input'
import { Button } from '@/components/ui/button'
import { useAuth } from '@/context/AuthContext'
import { Phone, Lock, Loader2 } from 'lucide-react'

const AuthModal = ({ isOpen, onClose, onLoginSuccess }) => {
	const { login } = useAuth()
	const [step, setStep] = useState(1) // 1: Telefon, 2: SMS kod
	const [phone, setPhone] = useState('+998')
	const [code, setCode] = useState('')
	const [timer, setTimer] = useState(120) // 2 minut = 120 sekund
	const [error, setError] = useState('')
	const [loading, setLoading] = useState(false)

	// Taymer logikasi
	useEffect(() => {
		let interval
		if (step === 2 && timer > 0) {
			interval = setInterval(() => setTimer(prev => prev - 1), 1000)
		}
		return () => clearInterval(interval)
	}, [step, timer])

	const formatTime = seconds => {
		const m = Math.floor(seconds / 60)
		const s = seconds % 60
		return `${m}:${s < 10 ? '0' : ''}${s}`
	}

	const handlePhoneSubmit = e => {
		e.preventDefault()
		if (phone.length < 13) {
			setError("To'g'ri raqam kiriting")
			return
		}
		setLoading(true)
		// SMS yuborish imitatsiyasi
		setTimeout(() => {
			setLoading(false)
			setStep(2)
			setTimer(120)
			setError('')
		}, 1000)
	}

	const handleCodeSubmit = e => {
		e.preventDefault()
		if (code === '123456') {
			login(phone)
			onLoginSuccess() // Muvaffaqiyatli kirganda checkoutni ochish uchun
			onClose()
			// Reset
			setStep(1)
			setCode('')
			setPhone('+998')
		} else {
			setError("Kod noto'g'ri! Qaytadan urinib ko'ring.")
		}
	}

	return (
		<Dialog open={isOpen} onOpenChange={onClose}>
			<DialogContent className='sm:max-w-[400px]'>
				<DialogHeader>
					<DialogTitle className='text-center text-xl font-bold'>
						{step === 1 ? "Kirish yoki ro'yxatdan o'tish" : 'Kodni kiriting'}
					</DialogTitle>
				</DialogHeader>

				{step === 1 ? (
					<form onSubmit={handlePhoneSubmit} className='space-y-4 mt-2'>
						<p className='text-sm text-gray-500 text-center'>
							Telefon raqamingizni kiriting va biz tasdiqlash kodini yuboramiz.
						</p>
						<div className='relative'>
							<Phone className='absolute left-3 top-2.5 h-5 w-5 text-gray-400' />
							<Input
								value={phone}
								onChange={e => setPhone(e.target.value)}
								className='pl-10 text-lg'
								placeholder='+998 90 123 45 67'
							/>
						</div>
						{error && (
							<p className='text-red-500 text-sm text-center'>{error}</p>
						)}
						<Button
							type='submit'
							className='w-full bg-orange-500 hover:bg-orange-600 h-12 text-lg'
							disabled={loading}
						>
							{loading ? <Loader2 className='animate-spin' /> : 'Kod olish'}
						</Button>
					</form>
				) : (
					<form onSubmit={handleCodeSubmit} className='space-y-4 mt-2'>
						<p className='text-sm text-gray-500 text-center'>
							{phone} raqamiga SMS kod yuborildi.
						</p>
						<div className='relative'>
							<Lock className='absolute left-3 top-2.5 h-5 w-5 text-gray-400' />
							<Input
								value={code}
								onChange={e => setCode(e.target.value)}
								className='pl-10 text-lg tracking-widest text-center'
								placeholder='123456'
								maxLength={6}
							/>
						</div>
						{error && (
							<p className='text-red-500 text-sm text-center'>{error}</p>
						)}

						<div className='text-center text-sm text-gray-500'>
							Qolgan vaqt:{' '}
							<span className='font-bold text-orange-500'>
								{formatTime(timer)}
							</span>
						</div>

						<Button
							type='submit'
							className='w-full bg-orange-500 hover:bg-orange-600 h-12 text-lg'
						>
							Tasdiqlash
						</Button>
						<Button
							type='button'
							variant='ghost'
							onClick={() => setStep(1)}
							className='w-full text-blue-600'
						>
							Raqamni o'zgartirish
						</Button>
					</form>
				)}
			</DialogContent>
		</Dialog>
	)
}

export default AuthModal
