'use client'

import React, { useState } from 'react'
import {
	Dialog,
	DialogContent,
	DialogHeader,
	DialogTitle,
} from '@/components/ui/dialog'
import { Button } from '@/components/ui/button'
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group'
import { Label } from '@/components/ui/label'
import { MapPin, Truck, CreditCard, Banknote, Map } from 'lucide-react'
import { useCart } from '@/context/CartContext'
import { useOrders } from '@/context/OrderContext'
import { useRouter } from 'next/navigation'

const CheckoutModal = ({ isOpen, onClose }) => {
	const router = useRouter()
	const { cartItems, totalCartCount, removeFromCart } = useCart() // removeFromCart o'rniga clearCart kerak, quyida yozamiz
	const { addOrder } = useOrders()

	// States
	const [step, setStep] = useState(1) // 1: Ma'lumotlar, 2: Tasdiqlash
	const [deliveryType, setDeliveryType] = useState('delivery') // 'delivery' or 'pickup'
	const [paymentType, setPaymentType] = useState('card') // 'card' or 'cash'

	// Cartdagi ma'lumotlarni hisoblash (taxminiy, chunki productsData bu yerda yo'q,
	// ideal variantda Products.json ni import qilish yoki Contextdan to'liq ma'lumot olish kerak.
	// Biz soddalashtirish uchun CartContextdan to'liq ma'lumot kelishini taxmin qilamiz yoki
	// bu yerda mahsulot narxlarini yana bir bor hisoblashimiz kerak bo'ladi.
	// Keling, CartContextni biroz update qilgan deb faraz qilamiz yoki oddiyroq yo'l tutamiz.

	// DEMO uchun statik ma'lumotlar:
	const deliveryPrice = deliveryType === 'delivery' ? 20000 : 0

	const handleOrder = () => {
		// 1. Buyurtma yaratish
		const newOrder = {
			items: cartItems, // ID va quantity
			totalCount: totalCartCount,
			deliveryType,
			paymentType,
			totalPrice: 0, // Bu yerda aniq summa bo'lishi kerak edi
		}

		addOrder(newOrder)

		// 2. Savatni tozalash (Bu funksiyani Contextga qo'shish kerak, hozircha qo'lda tozalaymiz deb faraz qilamiz)
		// Real loyihada clearCart() funksiyasi bo'lishi kerak.

		// 3. Yo'naltirish
		router.push('/orders')
		onClose()
	}

	return (
		<Dialog open={isOpen} onOpenChange={onClose}>
			<DialogContent className='sm:max-w-[600px] max-h-[80vh] overflow-y-auto'>
				<DialogHeader>
					<DialogTitle className='text-2xl font-bold'>
						Rasmiylashtirish
					</DialogTitle>
				</DialogHeader>

				{step === 1 ? (
					<div className='space-y-6 py-4'>
						{/* 1. Yetkazib berish turi */}
						<div>
							<h3 className='text-lg font-semibold mb-3'>
								1. Yetkazib berish usuli
							</h3>
							<div className='grid grid-cols-2 gap-4'>
								<div
									className={`border-2 rounded-xl p-4 cursor-pointer flex flex-col items-center gap-2 ${
										deliveryType === 'delivery'
											? 'border-orange-500 bg-orange-50'
											: 'border-gray-200'
									}`}
									onClick={() => setDeliveryType('delivery')}
								>
									<Truck
										className={`w-8 h-8 ${
											deliveryType === 'delivery'
												? 'text-orange-500'
												: 'text-gray-400'
										}`}
									/>
									<span className='font-medium'>Yetkazib berish</span>
								</div>
								<div
									className={`border-2 rounded-xl p-4 cursor-pointer flex flex-col items-center gap-2 ${
										deliveryType === 'pickup'
											? 'border-orange-500 bg-orange-50'
											: 'border-gray-200'
									}`}
									onClick={() => setDeliveryType('pickup')}
								>
									<MapPin
										className={`w-8 h-8 ${
											deliveryType === 'pickup'
												? 'text-orange-500'
												: 'text-gray-400'
										}`}
									/>
									<span className='font-medium'>Olib ketish</span>
								</div>
							</div>

							{/* Manzil Logikasi */}
							<div className='mt-4 p-4 bg-gray-50 rounded-lg border'>
								{deliveryType === 'delivery' ? (
									<div className='flex items-center gap-3 text-gray-700'>
										<MapPin className='text-blue-600' />
										<div>
											<p className='text-xs text-gray-400'>
												Sizning manzilingiz (Avtomatik):
											</p>
											<p className='font-semibold'>
												Toshkent sh, Yunusobod tumani, 12-mavze
											</p>
										</div>
									</div>
								) : (
									<div className='space-y-2'>
										<p className='font-semibold text-gray-700'>
											Bizning xaritadagi joylashuvimiz:
										</p>
										<div className='w-full h-32 bg-gray-200 rounded flex items-center justify-center text-gray-400 relative overflow-hidden'>
											<Map className='w-10 h-10 mb-2' />
											<div className='absolute inset-0 bg-blue-100 opacity-20'></div>
											<span className='z-10 text-sm'>
												Xarita (Punkt: Tech House Main)
											</span>
										</div>
									</div>
								)}
							</div>
						</div>

						{/* 2. To'lov turi */}
						<div>
							<h3 className='text-lg font-semibold mb-3'>2. To'lov usuli</h3>
							<RadioGroup
								value={paymentType}
								onValueChange={setPaymentType}
								className='space-y-3'
							>
								<div
									className={`flex items-center space-x-3 border p-3 rounded-lg ${
										paymentType === 'card'
											? 'border-orange-500 bg-orange-50'
											: ''
									}`}
								>
									<RadioGroupItem value='card' id='card' />
									<Label
										htmlFor='card'
										className='flex items-center gap-2 cursor-pointer flex-1'
									>
										<CreditCard className='text-blue-600' /> Karta orqali
										(Uzcard/Humo)
									</Label>
								</div>
								<div
									className={`flex items-center space-x-3 border p-3 rounded-lg ${
										paymentType === 'cash'
											? 'border-orange-500 bg-orange-50'
											: ''
									}`}
								>
									<RadioGroupItem value='cash' id='cash' />
									<Label
										htmlFor='cash'
										className='flex items-center gap-2 cursor-pointer flex-1'
									>
										<Banknote className='text-green-600' /> Naqd pul orqali
									</Label>
								</div>
							</RadioGroup>
						</div>

						<Button
							onClick={() => setStep(2)}
							className='w-full bg-blue-900 hover:bg-blue-800 h-12 text-lg'
						>
							Davom etish
						</Button>
					</div>
				) : (
					<div className='space-y-6 py-4'>
						{/* Tasdiqlash Bosqichi */}
						<div className='bg-gray-50 p-4 rounded-lg space-y-3'>
							<h3 className='font-bold border-b pb-2'>
								Ma'lumotlarni tekshiring
							</h3>

							<div className='flex justify-between text-sm'>
								<span className='text-gray-500'>Mahsulotlar soni:</span>
								<span className='font-medium'>{totalCartCount} dona</span>
							</div>
							<div className='flex justify-between text-sm'>
								<span className='text-gray-500'>Yetkazib berish:</span>
								<span className='font-medium'>
									{deliveryType === 'delivery'
										? "Kuryer orqali (20 000 so'm)"
										: 'Olib ketish (Bepul)'}
								</span>
							</div>
							<div className='flex justify-between text-sm'>
								<span className='text-gray-500'>Manzil:</span>
								<span className='font-medium truncate max-w-[200px]'>
									{deliveryType === 'delivery'
										? 'Toshkent sh, Yunusobod...'
										: 'Tech House Main'}
								</span>
							</div>
							<div className='flex justify-between text-sm'>
								<span className='text-gray-500'>To'lov turi:</span>
								<span className='font-medium'>
									{paymentType === 'card' ? 'Karta orqali' : 'Naqd pul'}
								</span>
							</div>
						</div>

						<div className='flex gap-3'>
							<Button
								variant='outline'
								onClick={() => setStep(1)}
								className='flex-1'
							>
								Orqaga
							</Button>
							<Button
								onClick={handleOrder}
								className='flex-1 bg-orange-500 hover:bg-orange-600 font-bold'
							>
								Tasdiqlash
							</Button>
						</div>
					</div>
				)}
			</DialogContent>
		</Dialog>
	)
}

export default CheckoutModal
